.projectSection {
  background-color: #ce9233;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  gap: 150px;

  padding: 120px 0 90px;
}

.projectSection p {
  color: white;
  font-size: 50px;
  font-weight: 900;

  text-transform: uppercase;
  margin-bottom: 30px;
}

.projectSection p:hover {
  color: black;
}
